import { AppDispatch, AppGetState } from "@/app/model/store"
import { onlineGeneralAPI } from "@/app/services/april-online-api"
import { BXUser } from "@/app/types/bitrix/bitrix-type"
import { departmentActions } from "./DepartmentSlice"
import { BXSDepartment, DEPARTAMENT_STATE_PROP, DUSER_ROLE } from "../type/department-type"
import { TESTING_DOMAIN, TESTING_USER } from "@/app/consts/app-global"
import { hookAPI } from "@/app/services/april-hook-api"
import { API_METHOD } from "@/app/types/api/api-type"

interface DepartmentSessionResponse {
    allUsers: Array<BXUser>
    childrenDepartments: Array<BXSDepartment>
    generalDepartment: Array<BXSDepartment>
}


export const getDepartment = () => async (
    dispatch: AppDispatch, getState: AppGetState
) => {
    // dispatch(setPreloader(true))

    const state = getState()
    const domain = state.app.domain || TESTING_DOMAIN //state.app.domain
    const currentUser = state.app.bitrix.user || TESTING_USER //state.app.currentUser

    const placement = state.event.placement


    let departament = null as null | Array<BXUser>

    const departamentData = { domain }


    // departament = await onlineGeneralAPI
    //     .service(
    //         'bitrix/departament',
    //         'post', 'departament',
    //         departamentData
    //     ) as Array<BXUser> | null

    const departamentResponse = await hookAPI
        .service(
            'full/department',
            API_METHOD.POST,
            'department',
            departamentData
        ) as DepartmentSessionResponse | null

    
    if (departamentResponse) {

        departament = departamentResponse.allUsers as Array<BXUser>


        dispatch(
            departmentActions
                .setFetchedDepartament({
                    domain,
                    placement,
                    departament,
                    currentUser
                }))

    }






}


export const setCurrentUser = (
    from: DEPARTAMENT_STATE_PROP.PLAN | DEPARTAMENT_STATE_PROP.REPORT,
    role: DUSER_ROLE, userId: number
) => async (
    dispatch: AppDispatch, getState: AppGetState
) => {
        const state = getState()
        const responsibilities = state.department[DEPARTAMENT_STATE_PROP.DEPARTAMENT][DUSER_ROLE.RESPONSIBLE].items
        const current = responsibilities.find((user: BXUser) => user.ID == userId)

        if (current) {
            dispatch(
                departmentActions
                    .setCurrentUser({
                        from,
                        role,
                        value: current
                    })
            )
        }

    }
