import { AppDispatch, AppGetState } from "@/app/model/store"
import { eventTaskActions } from "@/entities/EventTask"
import { EventItemResultType, eventItemActions } from "./EventItemSlice"
import { EventTask } from "@/entities/EventTask/types/event-task-type"
import { eventActions } from "@/processes/event"
import { eventPresentationActions } from "@/entities/EventPresentation"
import { PresentationProp } from "@/entities/EventPresentation/model/PresSlice"
import { eventSaleActions } from "@/entities/EventSale"
import { API_METHOD } from "@/app/types/api/api-type"
import { hookAPI } from "@/app/services/april-hook-api"
import { preloaderActions } from "@/shared/Preloader"
import { ROUTE_EVENT } from "@/processes/routes/types/router-type"




export const getResultMenu = (
    type: EventItemResultType, task: EventTask | null
) => async (
    dispatch: AppDispatch, getState: AppGetState
) => {
        dispatch(
            preloaderActions
                .setPreloader({ status: true })
        )
        const state = getState()
        const app = state.app
        const domain = app.domain
        const companyId = app.bitrix.placement.options.ID
        const userId = app.bitrix.user.ID

        if (task && task.id) {
            const rqformData = {
                domain,
                companyId,
                isFromTask: true,
                taskId: task.id,
                userId
            }
            const allTaskDeals = await hookAPI
                .service(
                    'full/supply',
                    API_METHOD.POST,
                    'deals',
                    rqformData
                ) as any




        }



        dispatch(eventTaskActions.setCurrentTask({
            task
        }))

        dispatch(eventItemActions.setEventItemMenuStatus({
            status: true,
            menuType: type
        }))

        dispatch(
            eventActions
                .setCurrentPage(
                    {
                        page: ROUTE_EVENT.ITEM
                    }

                )
        )

        //если текущее событие презентация и оно результативное
        if (task) {
            const isCrrentReportEventPres = task.eventType === 'presentation'

            if (isCrrentReportEventPres && type === EventItemResultType.RESULT) {
                dispatch(
                    eventPresentationActions
                        .setPresentationProp(
                            {
                                name: PresentationProp.IS_PRESENTATION_DONE,
                                value: true
                            }
                        )
                )



            }
        }

        dispatch(
            preloaderActions
                .setPreloader({ status: false })
        )
    }

export const cancelResultMenu = () => async (
    dispatch: AppDispatch, getState: AppGetState
) => {

    dispatch(
        eventSaleActions
            .setCurrentPresList({
                taskId: null
            })

    )


    dispatch(eventTaskActions.setCurrentTask({ task: null }))
    dispatch(eventItemActions.setEventItemMenuStatus({
        status: false,
        menuType: null
    }))
    dispatch(
        eventActions
            .setCurrentPage(
                {
                    page: ROUTE_EVENT.LIST
                }

            )
    )
}


export const finishResultMenu = () => async (
    dispatch: AppDispatch, getState: AppGetState
) => {
    dispatch(
        eventActions
            .setCurrentPage(
                {
                    page: ROUTE_EVENT.FINISH
                }

            )
    )
    dispatch(
        eventSaleActions
            .setCurrentPresList({
                taskId: null
            })

    )


    dispatch(eventTaskActions.setCurrentTask({ task: null }))
    dispatch(eventItemActions.setEventItemMenuStatus({
        status: false,
        menuType: null
    }))

}