import { FC, useEffect, useState } from "react"
import * as style from './DepartmentMode.module.scss';
import { BXUser } from "@/app/types/bitrix/bitrix-type"
import ASelect from "@/shared/ui/Inputs/Select/ASelect"
import { DEPARTAMENT_STATE_PROP, DepartmentModeStateItemCode, DUSER_ROLE, DUserStateItem } from "@/features/Departament/type/department-type"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { departmentActions } from "@/features/Departament"
import { setCurrentUser } from "../model/DepartmentThunk"
import { current } from "@reduxjs/toolkit";
import { eventReportActions } from "@/entities/EventReport";




export const DepartmentMode: FC = () => {

    const [isModeActive, setModeStatus] = useState(false)
    const departmentState = useAppSelector(state => state.department)
    const dMode = departmentState[DEPARTAMENT_STATE_PROP.MODE]

    const dispatch = useAppDispatch()
    const setCurrentMode = (type: DEPARTAMENT_STATE_PROP.MODE, value: string) => {

        let code = value as DepartmentModeStateItemCode
      
        dispatch(
            departmentActions.setMode(
                {
                    code
                }
            )
        )
        dispatch(
            eventReportActions.setMode(
                {
                    code
                }
            )
        )
    }


    useEffect(() => {
        setModeStatus(
            false
        )
    }, [dMode.current])

    return <div className="col-md-10 pt-4 align-right">

        {isModeActive
            ? <ASelect
            
                nameForHandler={DEPARTAMENT_STATE_PROP.MODE}
                handleChange={setCurrentMode}
                current={dMode.current}
                items={dMode.items}
                withLabel={false}
            />
            : <p className={style.status}
                onDoubleClick={() => setModeStatus(true)}
            >
                {dMode.current.name}
            </p>
        }

    </div>




}
