import { SendCard, SendCardActionProp } from '@/shared/ui/Cards/SendCard/SendCard';
import { FC } from 'react';
import * as classes from './NewTaskQuestion.module.scss';
interface NewTaskInitQuestionProps {
    action: () => void
}

export const NewTaskInitQuestion: FC<NewTaskInitQuestionProps> = ({ action }) => {
    const send = {
        color: 'blue',
        handler: action,
        title: 'НАЧАТЬ ПРОДАВАТЬ',
        isActive: true
    } as SendCardActionProp

    const cancel = {
        color: 'grey',
        handler: action,
        title: 'НЕ НАЧИНАТЬ',
        isActive: false
    } as SendCardActionProp

    return <SendCard
        title='Создание задачи'
        send={send}
        cancel={cancel}
        content={
            <div style={{ 'height': '300px' }}>
                <h3
                className={classes.title}
                >У вас нет задач по данной компании</h3>
            </div>
        }
    />
}

export default NewTaskInitQuestion;