import Report from "./Report"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { EV_REPORT_PROP, EventReportStateReport } from "../type/event-report-type"
import { eventReportActions } from "../model/EventReportSlice"
import { FC } from "react"

interface ReportContainerProps {
    isNoResult: boolean
}
export const ReportContainer: FC<ReportContainerProps> = ({
    isNoResult

}) => {

    const dispatch = useAppDispatch();
    const reportState = useAppSelector(state => state.eventReport)
    const report = reportState.report as EventReportStateReport


    const setEventProp = () => {
        dispatch(
            setEventProp
        )
    }

    const handleChange = (type: EV_REPORT_PROP, value: string) => {
        dispatch(
            eventReportActions.setReportProp({
                propName: type,
                value
            })
        )

    }

    return <Report
        report={report}
        isNoResult={isNoResult}
        handleChange={handleChange}
    />


}

