import { FC, useEffect, useState } from "react"
import { Col, Row, Badge } from "reactstrap"
import * as classes from './Presentation.module.scss'
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { PresentationCountProp, PresentationProp, PresentationStateCount, eventPresentationActions } from "../model/PresSlice"
import EVCard from "@/shared/ui/Cards/EventEntityCard/EventCard"



export const Presentation: FC = () => {

    // const [wasFixedPresentation, setWasFixedPresentation] = useState(false)
    //state
    const presentationState = useAppSelector(state => state.eventPresentation)
    const count = presentationState[PresentationProp.COUNT]
    // const companyCount = count[PresentationCountProp.COMPANY]
    // const currentCount = count[PresentationCountProp.SMART]
    const isPresentationDone = presentationState[PresentationProp.IS_PRESENTATION_DONE]
    const isUnplannedPresentation = presentationState[PresentationProp.IS_UNPLANNED_PRESENTATION]
    const currentTask = useAppSelector(state => state.eventTask.current)
    let currentTaskPresentation = null as PresentationStateCount | null
    const isCurrentReportPresentation = currentTask?.eventType == "presentation"
    if (currentTask) {
        if (currentTask.presentation) {
            currentTaskPresentation = currentTask.presentation
        }
    }
    const [presentstion, setPresentation] = useState(
        currentTaskPresentation
    )

    //dispatch
    const dispatch = useAppDispatch()
    const actions = eventPresentationActions
    const setPresProp = (name: PresentationProp, value: boolean) => {
        if (!isCurrentReportPresentation) {
            dispatch(
                actions.setPresentationProp(
                    { name, value }
                )
            )
        }

    }


    useEffect(() => {
        let currentTaskPresentation = null as PresentationStateCount | null
        if (currentTask) {
            if (currentTask.presentation) {
                currentTaskPresentation = currentTask.presentation
            }
        }
        setPresentation(
            currentTaskPresentation
        )

    }, [currentTask])

    return <EVCard title={'Презентации'} width={12}>
        <Col sm={12} className="p-0">
            <Row sm={12}
                className="p-0 d-flex flex-row justify-content-start align-items-start"
            >

                {presentstion && <Col sm={6}
                    className="d-flex flex-row justify-content-start align-items-start"
                >
                    <p className={`${classes.countName} p-0 col-md-12 col-form-label mt-0 d-flex flex-row
                                    justify-content-start align-items-center`}
                    >По компании: <span className={classes.countValue}>{presentstion[PresentationCountProp.COMPANY]}   </span> </p>

                    <p className={`${classes.countName} p-0 col-md-12 col-form-label m-0 d-flex flex-row
                                    justify-content-start align-items-center`}
                    >Текущие: <span className={classes.countValue}>{presentstion[PresentationCountProp.DEAL]}   </span></p>

                </Col>}

            </Row>

            <div

                className="d-flex flex-column justify-content-start align-items-start">
                <label
                    htmlFor="example-string-input"
                    className="col-md-12 mt-1 col-form-label mt-1"
                >
                    Зафиксировать презентацию
                </label>
                <div className={classes.actions}>
                    {/* <Badge
                                    style={{ cursor: 'pointer' }}
                                    className={"outline col-md-3 font-size-11 badge-soft-danger badge bg-danger "}
                                >
                                    {'Не было'}
                                </Badge> */}
                    <Badge
                        onClick={() =>
                            setPresProp(
                                PresentationProp.IS_PRESENTATION_DONE,
                                !isPresentationDone
                            )}
                        style={{ cursor: 'pointer' }}
                        outline

                        // @ts-ignore
                        color={false}
                        className={`col-md-12 
                                    ${!isPresentationDone
                                ? classes.badge
                                : classes.badge__active
                            }`}
                    >
                        {'Была проведена'}
                    </Badge>
                    {isPresentationDone && <div
                        className={classes.done__count}
                    >
                        +1
                    </div>}
                </div>

            </div>


            {/* <div className="col-md-12 mb-2 calling-btn-wrapper"> */}


            {/* <Button
                            disabled={!isFullComplete}
                            onClick={() => presentationDone()}
                            className="btn col-md-12 mb-2 calling-button call-button presentation" size='sm' >Проведена Презентация</Button> */}

        </Col>

    </EVCard >
}

