import { FC, useEffect, useState } from "react"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import EventItem from "./EventItem"
import { EV_REPORT_PROP } from "@/entities/EventReport/type/event-report-type"
import NewTaskInitQuestion from "./components/NewTaskQuestion"



export const EventItemContainer: FC = () => {

    const state = useAppSelector(state => state)
    const dispatch = useAppDispatch()

    const eventState = state.event
    const isFinish = eventState.isFinish


    const eventItemMenuState = state.eventItemMenu
    const menuType = eventItemMenuState.type
    const workStatus = state.eventReport.report[EV_REPORT_PROP.WORK_STATUS].current.code

    const taskState = useAppSelector(state => state.eventTask)
    const currentTask = taskState.current
    const isTasksFetched = taskState.isFetched
    const [isNeedCreate, setIsNeedCreate] = useState(false);




    const getwithPlan = () => (

        workStatus !== 'fail' && workStatus !== 'success'
    )

    const [withPlan, setWithPlan] = useState(
        getwithPlan()
    )


    useEffect(() => {
        setWithPlan(
            getwithPlan()
        )
    }, [

        workStatus
    ])




    const [isFetched, setIsFetched] = useState(false)
    const [isFinishComponent, setIsFinishComponent] = useState(isFinish)
    // useEffect(() => {
    //     if (!isFetched && !isFinishComponent) {
    //         initialCalling()

    //         setIsFetched(true)
    //     }

    // }, [isFetched, isFinishComponent])

    // useEffect(() => {

    //     if (isFinish) {
    //         setIsFetched(false)
    //     }
    //     setIsFinishComponent(isFinish)

    // }, [isFinish])

    
    return !currentTask && isTasksFetched && !isNeedCreate
        ? <NewTaskInitQuestion
            action={() => setIsNeedCreate(true)}
        />
        : <EventItem
            withPlan={withPlan}
            menuType={menuType}
        />

}

