import { combineReducers, configureStore, createListenerMiddleware } from '@reduxjs/toolkit';
import { eventTaskReducer } from '@/entities/EventTask/model/EventTaskSlice';
import { appReducer } from './AppSlice';
import { eventReducer, eventRouterReducer } from '@/processes/event';
import { routerReducer } from '@/processes/routes/model/RouterSlice';
import { eventItemReducer } from '@/widgets/EventItem/model/EventItemSlice';
import { eventReportReducer } from '@/entities/EventReport/model/EventReportSlice';
import { eventPresentationReducer } from '@/entities/EventPresentation';
import { eventPlanReducer } from '@/entities/EventPlan';
import { departmentReducer } from '@/features/Departament';
import { preloaderReducer } from '@/shared/Preloader';
import { eventSaleReducer } from '@/entities/EventSale/model/EventSaleSlice';
import { portalActions, portalAPI, portalReducer } from '@/entities/April/Portal';
import { documentRouterReducer } from '@/processes/konstructor';
import { documentComplectSettingsReducer } from '@/pages/konstructor/ComplectInitialSettings';
import { complectCurrentReducer, complectProfReducer, complectUniversalReducer } from '@/entities/KonstructorComplect';
import { infoblockReducer } from '@/entities/KonstructorInfoblock';
import { infoblockAPI } from '@/entities/KonstructorInfoblock/model/InfoblockService';
import { eventTaskActions, taskAPI } from '@/entities/EventTask';
import { getInitSale } from '@/entities/EventSale';



export const listenerMiddleware = createListenerMiddleware();




listenerMiddleware.startListening({
    actionCreator: eventTaskActions.setFetchedTasks,
    effect: async (action, listenerApi) => {
        
        const { dispatch } = listenerApi;
        // dispatch(initialEventTasks(action.payload.tasks));
        // dispatch(getInitSale(null, action.payload.tasks));
        // dispatch(updateLoggingForPortal(action.payload));
    },
});

// listenerMiddleware.startListening({
//     actionCreator: portalActions.setPortal,
//     effect: async (action, listenerApi) => {
        
//         const { dispatch } = listenerApi;
//         dispatch(getInitSale(action.payload.portal, null));
//         // dispatch(updateLoggingForPortal(action.payload));
//     },
// });



const rootReducer = combineReducers({
    app: appReducer,
    preloader: preloaderReducer,
    // [postAPI.reducerPath]: postAPI.reducer,
    //process
    router: routerReducer,
    event: eventReducer,
    eventRouter: eventRouterReducer,

    //widgets
    eventItemMenu: eventItemReducer,
    //entities
    eventTask: eventTaskReducer,
    [taskAPI.reducerPath]: taskAPI.reducer,
    eventReport: eventReportReducer,
    eventPresentation: eventPresentationReducer,
    eventPlan: eventPlanReducer,
    eventSale: eventSaleReducer,

    //features
    department: departmentReducer,



    documentRouter: documentRouterReducer,
    documentComplectSettings: documentComplectSettingsReducer,
    complectCurrent: complectCurrentReducer,
    complectProf: complectProfReducer,
    complectUniversal: complectUniversalReducer,
    infoblock: infoblockReducer,
    [infoblockAPI.reducerPath]: infoblockAPI.reducer,



    //april
    portal: portalReducer,
    [portalAPI.reducerPath]: portalAPI.reducer,


});



export const setupStore = () => {

    return configureStore({
        reducer: rootReducer,
        middleware: (getDefaultMiddleware) =>
            getDefaultMiddleware()
                .prepend(listenerMiddleware.middleware) // Добавление listener middleware в начало цепочки
                .concat(infoblockAPI.middleware)
                .concat(portalAPI.middleware)
                .concat(taskAPI.middleware)


    });
}

export type RootState = ReturnType<typeof rootReducer>;
export type AppStore = ReturnType<typeof setupStore>;
export type AppDispatch = AppStore['dispatch'];
export type AppGetState = AppStore['getState'];

