import  { FC } from 'react';
import * as classes from './EVCard.module.scss';


const EventCardAction: FC<{
    title: string
    hendler: () => void
}> = ({ title, hendler }) => {
    return (
        <h3 className={`${classes.action} p-0`}
            onClick={hendler}
        >{title}</h3>
    );
}

export default EventCardAction;