// import { IS_PROD } from '@/appglobal/global-consts';
import * as classes from './Gradient.module.scss';
import { FC, useEffect, useRef } from 'react';
import Logo from '../Logo/Logo';
import { useAppSelector } from '@/app/lib/hooks/redux';
import { APP_TYPE } from '@/app/types/app/app-type';
// import { bitrixAPI } from '@/services/bitrix-general-api';



interface GradientProps {
    isResized?: boolean
    isActive?: boolean
    isComponent?: boolean
    currentProcess?: APP_TYPE
    white?: boolean

}

const Gradient: FC<GradientProps> = ({
    isResized, isActive, isComponent, currentProcess, white = false
}) => {

    const scrollRef = useRef(null);
    useEffect(() => {
        if (!isComponent) {
            if (currentProcess !== APP_TYPE.EVENT) {


                if (!isResized) {
                    const asyncFunction = async () => {

                        // IS_PROD && await bitrixAPI.getResize(true)
                        const current = scrollRef.current
                        if (current) {
                            current.scrollIntoView({ behavior: 'smooth' });
                        }
                    }
                    asyncFunction()
                }

                const timer = setTimeout(() => {

                    const current = scrollRef.current
                    if (current) {
                        current.scrollIntoView({ behavior: 'smooth' });

                    }

                }, 400);

                return () => clearTimeout(timer);

            }
        }


    }, []);

    const containerClass = white ? classes.white : classes.monohrom


    return (
        <div
            ref={scrollRef}
            // className={isActive ? 'newgradient__containerApril' : 'monohrom__containerApril'}>
            className={`${classes.containerApril} ${containerClass} ${isComponent ? classes.cont__component : classes.cont__full}`}>

            <div className={`${classes.content__wrapper} ${isComponent ? classes.wrap__component : classes.wrap__full}`}>

                <div className={classes.newchildren__wrapper}

                >

                    <Logo />
                </div>
                {isActive && <p className={classes.loader__title}>Загрузка  . . .</p>}
            </div>

        </div>
    )
}
export default Gradient