import { useAppSelector } from '@/app/lib/hooks/redux';
import { getCurrentWorkStatusItems } from '@/entities/EventReport/lib/work-status-util';
import { EV_REPORT_PROP, EventReportStateReport } from '@/entities/EventReport/type/event-report-type';
import { DEPARTAMENT_STATE_PROP } from '@/features/Departament/type/department-type';
import ASelect from '@/shared/ui/Inputs/Select/ASelect';
import React, { FC, useEffect, useState } from 'react';

interface WorkStatusProps {
    report: EventReportStateReport
    handleChange: (type: EV_REPORT_PROP, value: string) => void
}

const WorkStatus: FC<WorkStatusProps> = ({
    report,
    handleChange
}) => {

    const departmentMode = useAppSelector(state => state.department[DEPARTAMENT_STATE_PROP.MODE].current.code)

    const workStatusItems = getCurrentWorkStatusItems(
        report,
        departmentMode
    )
    const workStatusCurrent = report[EV_REPORT_PROP.WORK_STATUS].current
    const [currentworkStatusItems, setCurrentWorkStatusItems] = useState(workStatusItems)


    useEffect(() => {
        const workStatusItems = getCurrentWorkStatusItems(
            report,
            departmentMode
        )
        setCurrentWorkStatusItems(
            workStatusItems
        )



    }, [
   
        report
    ])


    return (
        <ASelect
            label={'Статус Работы'}
            nameForHandler={EV_REPORT_PROP.WORK_STATUS}
            handleChange={handleChange}
            current={report[EV_REPORT_PROP.WORK_STATUS].current}
            items={currentworkStatusItems}
        />
    );
}

export default WorkStatus;