import { useAppSelector } from '@/app/lib/hooks/redux'
import { FC } from 'react'
import Gradient from '../../ui/Backgrounds/Gradient'
import { RouterState } from '@/processes/routes/model/RouterSlice'
import { APP_TYPE } from '@/app/types/app/app-type'
import EVCard from '@/shared/ui/Cards/EventEntityCard/EventCard'
import * as classes  from './Preloader.module.scss'


export const PreloaderCard: FC<{}> = () => {
    const router = useAppSelector(state => state.router) as RouterState
    const currentProcess = useAppSelector(state => state.router.current) as APP_TYPE


    return (
        <EVCard title={''} width={12}>
            <div className={classes.gradientWrapper}>
                <Gradient
                    isComponent={true}
                    currentProcess={currentProcess}
                    isActive={true}
                    white
                />
            </div>

        </EVCard>


    )
}
