import { FC, ReactNode } from 'react'
import * as classes from './SendCard.module.scss'
import EVCard from '@/shared/ui/Cards/EventEntityCard/EventCard';
import { AButton, ComponentPropsColors } from '@/shared/ui/Button/AAButton';
import EventCardAction from '../EventEntityCard/CardAction';


export type SendCardActionProp = {
    color: ComponentPropsColors
    title: string
    handler: () => void
    isActive: boolean
}

interface SendCardProps {
    title: string
    content: ReactNode
    send?: SendCardActionProp,
    cancel?: SendCardActionProp,
    action?: SendCardActionProp

}

export const SendCard: FC<SendCardProps> = ({
    title,
    content,
    send,
    cancel,
    action
}) => {



    // const scrollRef = useRef(null);
    // useEffect(() => {
    //     const current = scrollRef.current
    //     if (current) {
    //         current.scrollIntoView({ behavior: 'smooth' });

    //     }
    // }, [status]);


    return (

        <EVCard title={title} width={6}
            size="big"
            actionComponent={
                action && <EventCardAction
                    title={action.title}
                    hendler={action.handler}
                />
            }
        >
            {content}
            <div className={classes.footer}>
                <div className={classes.btnWrapper}>
                    {send && <AButton
                        title={send.title}
                        clickHendler={send.handler}
                        color={send.color}
                        size='big'
                        isActive={send.isActive}
                    />}
                </div>
                {cancel && <AButton
                    title={cancel.title}
                    clickHendler={cancel.handler}
                    color={cancel.color}
                    size='big'
                    isActive={false}
                />}
            </div>
        </EVCard>


    )
}


