import { initialEventTasks, taskAPI } from "@/entities/EventTask"
import { DEV_CURRENT_USER_ID, TESTING_DOMAIN, TESTING_PLACEMENT, TESTING_USER } from "../consts/app-global"
import { bitrixAPI } from "../services/bitrix-general-api"
import { BXTask, BXUser } from "../types/bitrix/bitrix-type"
import { Placement } from "../types/bitrix/placement-type"
import { appActions } from "./AppSlice"
import { AppDispatch, AppGetState } from "./store"
import { departmentActions, getDepartment } from "@/features/Departament"
import { portalAPI } from "@/entities/April/Portal"

import { EntitiesFromPlacement, getDisplayMode, getEntitiesFromPlacement } from "../lib/utils/placement-util"
import { localAPI } from "../services/local"
import { APP_DISPLAY_MODE } from "../types/app/app-type"
import { initialTasksFromCurrentTask } from "@/entities/EventTask/model/EventTaskThunk"
import { EventTask } from "@/entities/EventTask/types/event-task-type"
import { getInitSale } from "@/entities/EventSale"
import { getEvTasksFromBxTasks } from "@/entities/EventTask/lib/task-util"
import { getResultMenu } from "@/widgets/EventItem"
import { EventItemResultType } from "@/widgets/EventItem/model/EventItemSlice"

export const initial = () => async (
    dispatch: AppDispatch, getState: AppGetState
) => {





    const currentDepartmentMode = await localAPI.getParsedData('currentDepartmentMode')

    if (currentDepartmentMode) {
        if (currentDepartmentMode.code) {
            dispatch(
                departmentActions
                    .setMode({ code: currentDepartmentMode.code })
            )
        }
    }

    console.log('app initial')
    //@ts-ignore
    // console.log(window.BX24)



    const state = getState()

    const currentRoute = state.router.current

    const fetchedDdomain = __IN_BITRIX__ ? await bitrixAPI.getDomain() : null
    let domain = fetchedDdomain ? fetchedDdomain : TESTING_DOMAIN
    console.log('domain')
    //@ts-ignore
    console.log(domain)

    let companyPlacement = {
        placement: 'COMPANY',
        options: {
            ID: 0
        }
    } as Placement
    let currentCompany = null
    let currentDeal = null


    let placement = __IN_BITRIX__
        ? await bitrixAPI.getPlacement() as Placement | null
        : TESTING_PLACEMENT
    const user = __IN_BITRIX__ ? await bitrixAPI.getCurrentUser() as BXUser : TESTING_USER
    // await auth.getSanctumTest()


    console.log('app placement')

    console.log(placement)
    /**
     * 
     * FOR CONFIGURABLE ACTIVITY
     */
    //@ts-ignore
    if (placement.options.placement) {
        //@ts-ignore
        placement = placement.options
    }

    const entitiesFromPlacement = await getEntitiesFromPlacement(
        placement,
        domain
    ) as EntitiesFromPlacement
    
    if (entitiesFromPlacement) {
        if (entitiesFromPlacement.currentCompany) {


            currentCompany = entitiesFromPlacement.currentCompany
            currentDeal = entitiesFromPlacement.currentDeal
            companyPlacement = entitiesFromPlacement.companyPlacement
            const currentTask = entitiesFromPlacement.currentTask
            // const isDetail = isDetailPlacement(placement)
            // const isActivity = isActivityPlacement(placement)
            // const isTask = isTaskPlacement(placement)
            const displayMode = getDisplayMode(placement)

            if (displayMode == APP_DISPLAY_MODE.ENTITY_CARD) {
                __IN_BITRIX__ && await bitrixAPI.getFit()
            }

            dispatch(
                appActions.
                    setAppData(
                        {
                            domain,
                            user,
                            placement: companyPlacement,
                            company: currentCompany,
                            deal: currentDeal,
                            display: displayMode,
                            // isDetail,
                            // isTask,
                            task: currentTask

                        }
                    ))

            if (!currentTask) {
                const userId = user['ID'] || DEV_CURRENT_USER_ID
                const data = {
                    domain: domain || TESTING_DOMAIN,
                    userId: userId,
                    placement: companyPlacement,
                    currentTaskId: currentTask?.id

                }

                console.log('get tasks data')
                console.log(data)
                
                dispatch(initialEventTasks(
                    null,
                    userId,
                    placement.options.ID,
                    domain || TESTING_DOMAIN

                ))
                // dispatch(taskAPI.endpoints.fetchTasks.initiate(data));


            } else {
                const allTasksFromCurrentTask = [currentTask] as BXTask[]
                const resultEventTasks = getEvTasksFromBxTasks(allTasksFromCurrentTask)
                dispatch(
                    initialTasksFromCurrentTask(
                        resultEventTasks
                    ))

                
                    console.log('resultEventTasks')
                    console.log(resultEventTasks)
                dispatch(
                    getInitSale(
                        null, resultEventTasks
                    ));


            }



            dispatch(getDepartment())
            dispatch(
                appActions
                    .setInitializedSuccess({})
            )

            // const getPortaldata = {
            //     domain: domain || TESTING_DOMAIN,
            //     userId: user['ID'],

            // }
            // dispatch(portalAPI.endpoints.fetchPortal.initiate({ domain }));


            // dispatch(setInitialRoute(APP_TYPE.CALLING, false))
            // dispatch(setPreloader(false))
        }
        
    } else {
        
        dispatch(appActions.setInitializedError({ errorMessage: 'Компания не найдена' }))
    }


}


