import { FC, useEffect, useState } from "react"
import * as classes from './Plan.module.scss'
import { EV_PLAN_CODE, EV_PLAN_PROP } from "../type/event-plan-type"
import { EV_PLAN_STATE_ITEM } from "../model/EventPlanSlice"
import ASelect from "@/shared/ui/Inputs/Select/ASelect"
import { DEPARTAMENT_STATE_PROP, DUSER_ROLE } from "@/features/Departament/type/department-type"
import EVCard from "@/shared/ui/Cards/EventEntityCard/EventCard"
import { Col } from "reactstrap"
import AInput from "@/shared/ui/Inputs/Input/AInput"
import ADate from "@/shared/ui/Inputs/Date/ADate"
import EventCardAction from "@/shared/ui/Cards/EventEntityCard/CardAction"
import { Department } from "@/features/Departament"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { EventItemResultType } from "@/widgets/EventItem/model/EventItemSlice"
import { ErrorsCode, eventActions } from "@/processes/event/model/EventSlice"


interface PlanProp {
    date: string
    type: EV_PLAN_STATE_ITEM
    isNoResultMenu: boolean
    planPropChange: (type: EV_PLAN_PROP, value: string) => void

}

const Plan: FC<PlanProp> = ({
    date,
    type,
    isNoResultMenu,
    planPropChange,
}) => {


    // const [isRequiredError, setIsRequiredError] = useState(false)
    // const [nameErrorMessge, setIsNameErrorMessge] = useState('')
    const [isDepartmentShow, setIsDepartmentShow] = useState(false)
    const [planActionName, setPlanActionName] = useState('Сотрудник')


    const currentDepartmentMode = useAppSelector(state => state.department[DEPARTAMENT_STATE_PROP.MODE].current)
    const isTmcMode = currentDepartmentMode.code === 'tmc'


    const menuType = useAppSelector(state => state.eventItemMenu.type) 

    const isNoResult = menuType === EventItemResultType.NORESULT

    const isNoresultTmc = isTmcMode && isNoResult
    const isPresentationPlaning = type.current.code === EV_PLAN_CODE.PRESENTATION

    const dispatch = useAppDispatch()

    const handleOnNameFocus = (code: EV_PLAN_PROP, error: string) => {
        if (error) {
            if (code) {
                if (code === EV_PLAN_PROP.NAME || code === EV_PLAN_PROP.CONTACT_NAME || code === EV_PLAN_PROP.CONTACT_PHONE || code === EV_PLAN_PROP.CONTACT_EMAIL) {
                    const resCode = code as ErrorsCode

                    dispatch(
                        eventActions
                            .setError(
                                {
                                    code: resCode,
                                    value: ''
                                }
                            )
                    )
                }
            }


        }


    }

    const planModeHendler = () => {
        let actionName = 'Сотрудник'
        if (!isDepartmentShow) {
            actionName = 'Вернуться'
        }
        setPlanActionName(actionName)
        setIsDepartmentShow(!isDepartmentShow)
    }

    //  <React.Fragment>

    const errors = useAppSelector(state => state.event.errors.current)

    return <EVCard title={'Планирование'}
        width={12}
        size={(isNoresultTmc || !isTmcMode || (isTmcMode && !isPresentationPlaning)) ? 'big' : "full"}
        actionComponent={
            !isTmcMode && <EventCardAction
                title={planActionName}
                hendler={planModeHendler}
            />
        }
    >

        {isDepartmentShow ?
            <Department
                from={DEPARTAMENT_STATE_PROP.PLAN}
            />
            : <>
                {!isNoResultMenu && <AInput
                    label={'Название - что нужно сделать'}
                    errorMessage={errors[EV_PLAN_PROP.NAME]}
                    nameForHandler={EV_PLAN_PROP.NAME}
                    handleChange={planPropChange}
                    handleOnFocus={handleOnNameFocus}
                />}
                <ADate
                    label={'Дата и время звонка'}
                    value={date}
                    // errorMessage={null}
                    nameForHandler={EV_PLAN_PROP.DATE}
                    handleChange={planPropChange}
                // handleOnFocus={false}
                />
                {!isNoResultMenu && <ASelect
                    label={'Тип звонка'}
                    nameForHandler={EV_PLAN_PROP.TYPE}
                    handleChange={planPropChange}

                    current={type.current}
                    //@ts-ignore
                    items={type.items}
                />}
                {!isNoResultMenu && isTmcMode && isPresentationPlaning && <>
                    <AInput
                        label={'ФИО контактного лица'}
                        errorMessage={errors[EV_PLAN_PROP.CONTACT_NAME]}
                        nameForHandler={EV_PLAN_PROP.CONTACT_NAME}
                        handleChange={planPropChange}
                        handleOnFocus={handleOnNameFocus}
                    />
                    <AInput
                        label={'Телефон контактного лица'}
                        errorMessage={errors[EV_PLAN_PROP.CONTACT_PHONE]}
                        nameForHandler={EV_PLAN_PROP.CONTACT_PHONE}
                        handleChange={planPropChange}
                        handleOnFocus={handleOnNameFocus}
                    />
                    <AInput
                        label={'E-mail контактного лица'}
                        errorMessage={errors[EV_PLAN_PROP.CONTACT_EMAIL]}
                        nameForHandler={EV_PLAN_PROP.CONTACT_EMAIL}
                        handleChange={planPropChange}
                        handleOnFocus={handleOnNameFocus}
                    />

                    <Department
                        from={DEPARTAMENT_STATE_PROP.PLAN}
                    />
                </>
                }
            </>
        }
        {/* </Col> */}
    </EVCard>


}

export default Plan