import { EV_PLAN_PROP, EventPlanCall } from "@/entities/EventPlan/type/event-plan-type";
import { EV_REPORT_PROP, EventReportSelectItem } from "@/entities/EventReport/type/event-report-type";
import { FC } from "react";
import ALabel from "../Label/ALabel";
import * as style from '../Input.module.scss'
import { DEPARTAMENT_STATE_PROP, DepartmentModeStateItem, DUSER_ROLE } from "@/features/Departament/type/department-type";
import { BXUser } from "@/app/types/bitrix/bitrix-type";
import { ComplectSettingsSelectItem, GLOBAL_STATE_PROP } from "@/pages/konstructor/ComplectInitialSettings/model/ComplectSettingsSlice";


interface ASelectProps {
    label?: string,
    withLabel?: boolean
    nameForHandler: EV_REPORT_PROP | EV_PLAN_PROP | DUSER_ROLE | GLOBAL_STATE_PROP | DEPARTAMENT_STATE_PROP.MODE,
    handleChange: (
        type: EV_REPORT_PROP | EV_PLAN_PROP | DUSER_ROLE | DEPARTAMENT_STATE_PROP.MODE,
        value: string | number

    ) => void,
    current: EventReportSelectItem | BXUser | DepartmentModeStateItem | EventPlanCall,
    items: Array<EventReportSelectItem> | Array<BXUser> | Array<DepartmentModeStateItem>
}


const ASelect: FC<ASelectProps> = ({
    label,
    withLabel = true,
    nameForHandler,
    handleChange,
    current,
    items

}) => {
    const id = `input-${label}-select-${nameForHandler}`;


    const isEventReportSelectItem = (variable: any): variable is EventReportSelectItem => {
        return variable && (variable as EventReportSelectItem).code !== undefined;

    };

    const isDMode = (variable: any): variable is DepartmentModeStateItem => {
        return variable && (variable as DepartmentModeStateItem).code !== undefined;
    };

    const isBXUser = (variable: any): variable is BXUser => {
        return variable && (variable as BXUser).ID !== undefined;
    };


    const isComplectSetting = (variable: any): variable is ComplectSettingsSelectItem => {
        return variable && (variable as ComplectSettingsSelectItem).id !== undefined;
    };

    const isEventPlan = (variable: any): variable is EventPlanCall => {
        return variable && (variable as EventPlanCall).id !== undefined;
    };



    const isEventReport = isEventReportSelectItem(current) || isDMode(current) || isEventPlan(current)

    let value = isEventReport ? current.code : current.ID;

    return (
        <>

            <div className={`${style.inputWrapper} col-md-12`}>
                {withLabel && <ALabel
                    htmlId={id}
                    label={label}
                    errorMessage={null}

                />}

                <select className={`${style.input} form-control `}
                    value={value}
                    //@ts-ignore
                    onChange={(e) => handleChange(nameForHandler, e.target.value)}
                >
                    {items.map(item => {
                        if (isEventReportSelectItem(item) || isDMode(item)) {
                            return (
                                <option key={`${nameForHandler}-${item.id}`} value={item.code || item.id}>{item.name}</option>
                            );
                        } else if (isBXUser(item)) {
                            return (

                                <option key={`${nameForHandler}-${item.ID}`} value={item.ID}>{item.NAME} {item.LAST_NAME}</option>
                            );
                        } else if (isComplectSetting(item)) {
                            const curentComplectSettingItem = item as ComplectSettingsSelectItem
                            return (

                                <option key={`${nameForHandler}-${curentComplectSettingItem.id}`} value={curentComplectSettingItem.id}>{curentComplectSettingItem.title}</option>
                            );
                        }
                    })}
                </select>
            </div>

        </>
    );
}

export default ASelect;