import { FC } from "react";
import { EV_PLAN_PROP } from "@/entities/EventPlan/type/event-plan-type";
import ALabel from "../Label/ALabel";
import * as style from '../Input.module.scss'
import { ErrorsCode } from "@/processes/event/model/EventSlice";


interface AInputProps {
    label: string | null,
    errorMessage: string | null,
    nameForHandler: EV_PLAN_PROP,
    handleChange: (
        type: EV_PLAN_PROP, value: string
    ) => void,
    handleOnFocus: (code: EV_PLAN_PROP, error: string) => void

}


const AInput: FC<AInputProps> = ({
    label,

    errorMessage,
    nameForHandler,
    handleChange,
    handleOnFocus,

}) => {
    const id = `input-${nameForHandler}`;
    return (
        <>
            <div className={`${style.inputWrapper} col-md-12`}>
                {label && <ALabel
                    htmlId={id}
                    label={label}
                    errorMessage={errorMessage}

                />}
                <input
                    onChange={
                        (e) => handleChange(
                            nameForHandler,
                            e.target.value
                        )}

                    onFocus={() => handleOnFocus(nameForHandler, errorMessage)}
                    className={`${style.input} form-control ${errorMessage ? 'is-invalid' : ''}`}
                    // type="text"
                    // className={`form-control is-invalid`}
                    // defaultValue="2019-08-19"
                    id="example-string-input"
                    lang="rus"
                />
            </div>

        </>
    );
}

export default AInput;