import React, { FC, useState } from 'react';
import { FormGroup, Input, Label } from 'reactstrap';
import * as style from './Infoblock.module.scss'
import { AIBlock } from '@/entities/KonstructorInfoblock/type/document-infoblock-type';



interface IBlockProps {
  iblock: AIBlock
  isActive: boolean
}

export const Infoblock: FC<IBlockProps> = ({
  iblock,
  isActive
}) => {
  const [text, setText] = useState(iblock.description); // Начальное значение текста
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setText(event.target.value); // Обновление текста при изменении ввода
  };

  return <FormGroup check className="col-md-12"
    style={{ cursor: 'pointer' }}
  // onClick={() => setIsWord(!isActive)}
  >
    <Label check className="col-md-12 mb-0 d-flex align-items-center justify-content-start">
      <Input
        type="checkbox"
        id="flexCheckDefault"
        className={`mb-1 me-1 ${isActive
          ? 'word__checkbox__active'
          : 'word__checkbox'} `

        }
        color="primary"
        // style={{ lineHeight: 0 }}
        value={1}
        checked={isActive}
        key="select-isOneMore"
      // onChange={(e) => {
      //     setIsWord()
      //     // console.log('word click', e.target.checked)

      // }}
      />
      <p className="mb-1 ml-3">{iblock.name}</p>
      <input
        type="text"
        value={text}
        onChange={handleChange}
        className={style.description}
      />
    </Label>

  </FormGroup>;
}
