import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { initialEventApp } from "../model/EventThunk";
import { FC, Suspense, useEffect, useState } from "react";
import { EventListPage } from "@/pages/event/ListPage";
import EventItemPage from "@/pages/event/ItemPage/ui/EventItemPage";
import { Header } from "@/widgets/Header";
import { Preloader } from "@/shared/Preloader";
import { EventTask } from "@/entities/EventTask/types/event-task-type";
import { getInitPresentation } from "@/entities/EventPresentation";
import { getInitSale } from "@/entities/EventSale";
import { ROUTE_EVENT } from "@/processes/routes/types/router-type";
import { FinishPage } from "@/pages/event/FinishPage";




export const EventContainer: FC = () => {
    const dispatch = useAppDispatch();
    const currentPage = useAppSelector(state => state.event.currentPage);
    const isMenu = useAppSelector(state => state.eventItemMenu.isActive);

    const isFinish = useAppSelector(state => state.event.isFinish)
    const isTasksFetched = useAppSelector(state => state.eventTask.isFetched)


    const initialEventReport = () => {

        dispatch(
            initialEventApp()
        )
    }

    const appError = useAppSelector(state => state.app.error)

    const [errorMessage, setErrorMesage] = useState(appError.message)
    

    const [isFetched, setIsFetched] = useState(false)
    const [isFinishComponent, setIsFinishComponent] = useState(isFinish)
    const [isTasksComplete, setIsTasksComplete] = useState(isTasksFetched)
    const [isItemMenu, setIsItemMenu] = useState(false)

    const [route, setCurrentRoute] = useState(currentPage)



    useEffect(() => {
        if (!isFetched && !isFinishComponent) {
            initialEventReport()
            setIsFetched(true)

        }

    }, [isFetched, isFinishComponent])

    useEffect(() => {

        if (isFinish) {
            setIsFetched(false)
        }
        setIsFinishComponent(isFinish)

    }, [isFinish])





    useEffect(() => {
        setIsTasksComplete(isTasksFetched)
        if (isTasksFetched) {
            dispatch(
                getInitPresentation()
            )

            // dispatch(
            //     getInitSale()
            // )
        }
    }, [isTasksFetched])




    useEffect(() => {
        if (isTasksFetched) {
            const isItemMenu = currentPage == 'item' && isMenu
            setIsItemMenu(
                isItemMenu
            )

        }


    }, [
        isTasksFetched,
        currentPage,
        isMenu
    ])
    useEffect(() => {

        setCurrentRoute(
            currentPage
        )

    }, [

        currentPage

    ])



    useEffect(() => {


        setErrorMesage(appError.message)
        

    }, [appError])



    let component = <Preloader />


    if (errorMessage) {
        
        return <div>{errorMessage} </div>
    }


    if (isTasksFetched || currentPage == ROUTE_EVENT.FINISH) {

        switch (route) {
            case ROUTE_EVENT.ITEM:

                component = <Suspense fallback={<Preloader />}>
                    <EventItemPage />
                </Suspense>
                break;


            case ROUTE_EVENT.LIST:
                // if (!tasksCount) {
                //     component = <Suspense fallback={<Preloader />}>
                //         <EventItemPage />
                //     </Suspense>
                // }
                component = <Suspense fallback={<Preloader />}>
                    <EventListPage />
                </Suspense>
                break;

            case ROUTE_EVENT.FINISH:
                // if (!tasksCount) {
                //     component = <Suspense fallback={<Preloader />}>
                //         <EventItemPage />
                //     </Suspense>
                // }

                component = <Suspense fallback={<Preloader />}>
                    <FinishPage />
                </Suspense>
                break;
            default:
                component = <Preloader />
        }
    }

    return component;
}


