import React, { FC } from "react"
import * as classes from './EventItem.module.scss'
import { Button, Col, Row, } from "reactstrap"
import { ReportContainer } from "@/entities/EventReport"
import { EventItemResultType } from "../model/EventItemSlice"
import { Presentation } from "@/entities/EventPresentation"
import { Plan } from "@/entities/EventPlan"
import { Comment } from "@/entities/EventComment"
import 'bootstrap/dist/css/bootstrap.min.css';
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { send } from "@/processes/event"
import { cancelResultMenu } from "../model/EventItemThunk"
import { EV_REPORT_PROP } from "@/entities/EventReport/type/event-report-type"
import { EventSale } from "@/entities/EventSale"
import { navigate } from "@/processes/routes"
import { ROUTE_DOCUMENT, ROUTE_EVENT } from "@/processes/routes/types/router-type"
import { APP_DISPLAY_MODE, APP_TYPE } from "@/app/types/app/app-type"
import { DepartmentMode } from "@/features/Departament/ui/DepartmentMode"
import { DEPARTAMENT_STATE_PROP } from "@/features/Departament/type/department-type"

interface EventItemProps {
    menuType: EventItemResultType,
    withPlan: boolean,

}




const EventItem: FC<EventItemProps> = ({
    menuType,
    withPlan,

}) => {
    const dispatch = useAppDispatch();
    const sendEvent = () => {
        dispatch(
            send()
        )
    }

    const cancel = () => {
        dispatch(
            cancelResultMenu()
        )
    }

    const toDocument = () => {

        dispatch(
            navigate(
                APP_TYPE.DOCUMENT,
                ROUTE_DOCUMENT.COMPLECT_SETTINGS
            )
        )
    }
    const workStatus = useAppSelector(state => state.eventReport.report[EV_REPORT_PROP.WORK_STATUS].current.code)
    const app = useAppSelector(state => state.app)
    const sale = useAppSelector(state => state.eventSale)
    const currentDepartmentMode = useAppSelector(state => state.department[DEPARTAMENT_STATE_PROP.MODE].current)

    const isTmcMode = currentDepartmentMode.code === 'tmc'

    const placementType = app.bitrix.placement.placement
    const inEntityCard = app.display.mode === APP_DISPLAY_MODE.ENTITY_CARD

    const isSaleFetched = sale.isFetched

    const isNoResult = menuType === EventItemResultType.NORESULT

    const isNoresultTmc = isTmcMode && isNoResult

    
    return <React.Fragment>

        <div className={inEntityCard ? classes.wrapperDetail : classes.wrapper}>

            <Row className={`p-10`}>

                <>
                    <Col sm={5} xs={12} className={classes.area}>
                        {/* <Row> */}
                        <div className={`${classes.cardWrapper} ${classes.report} p-0`}>
                            <ReportContainer
                                isNoResult={false}
                            />
                        </div>
                        {/* <Col className={classes.line} sm={1}></Col> */}
                        <div className={`${classes.cardWrapper} mt-2 p-0`}>
                            <Comment />
                        </div>
                        {/* </Row> */}
                    </Col>
                    <Col sm={5} xs={12} className={classes.area + " " + classes.left}>
                        {/* <Row> */}
                        {!isTmcMode && <div className={classes.presentation}>

                            {menuType === 'result' || menuType === 'new'
                                ? <Presentation />
                                : <ReportContainer isNoResult={true} />
                            }


                        </div>}
                        {isTmcMode && <div className={classes.presentation}>

                            {menuType !== 'result' && menuType !== 'new'
                                && <ReportContainer isNoResult={true} />
                            }


                        </div>}
                        <div className={`${(isNoresultTmc || !isTmcMode) ? 'mt-2' : ' mt-0'} p-0`}>
                            {/* <div className={`mt-2`}> */}

                            {withPlan && <Plan />}
                            {workStatus === 'success' && <EventSale />}
                        </div>


                    </Col>
                </>



                <Col sm={2} xs={12} className={`${classes.actionsWrapper} p-0`}>
                    <div className={classes.actions}>

                        <Button className={`${classes.btn_send} col-10`} size='sm'
                            onClick={sendEvent}
                        >
                            ОТПРАВИТЬ
                        </Button>
                        {/* <Button className={classes.btn_document + ' col-md-10'} size='sm'
                            onClick={toDocument}
                        >
                            ДОКУМЕНТЫ
                        </Button> */}

                        <Button className={`${classes.btn_cancel + ' col-10'}`} size='sm'
                            onClick={cancel}
                        >
                            ОТМЕНА
                        </Button>
                        <DepartmentMode />
                    </div>

                </Col>
            </Row>

        </div>

    </React.Fragment >
}

export default EventItem