import { FC } from "react";
import { EV_PLAN_PROP } from "@/entities/EventPlan/type/event-plan-type";
import ALabel from "../Label/ALabel";
import * as style from '../Input.module.scss'

interface ADateProps {
    label: string | null,
    value?: string,
    errorMessage?: string | null,
    nameForHandler: EV_PLAN_PROP.DATE,
    handleChange: (
        type: EV_PLAN_PROP, value: string
    ) => void,
    // handleOnFocus: () => void

}


const ADate: FC<ADateProps> = ({
    label,
    value,
    errorMessage,
    nameForHandler,
    handleChange,
    // handleOnFocus,

}) => {
    const id = `input-${nameForHandler}`;
    return (
        <>


            <div className={`${style.inputWrapper} col-md-12`}>
                {label && <ALabel
                    htmlId={id}
                    label={label}
                    errorMessage={errorMessage}

                />}

                <input
                    className={`${style.input} form-control mt-0`}
                    type="datetime-local"
                    defaultValue={value}
                    id={id}
                    lang="rus"
                    onChange={(e) => handleChange(nameForHandler, e.target.value)}
                />
            </div>

        </>
    );
}

export default ADate;