import * as classes from "./Complect.module.scss"
import React, { FC, useState } from 'react';
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux";
import { FormGroup, Input, Label } from "reactstrap";
//@ts-ignore
import { useFetchAllInfoblocksQuery } from '../model/InfoblockService';
import { DGroup } from "../model/InfoblockSlice";
import { Infoblock } from "./components/Infoblock/Infoblock";
import { AIBlock } from "../type/document-infoblock-type";


export const DInfoblocks: FC = ({


}) => {
    
    const dispatch = useAppDispatch()
    const infoblockState = useAppSelector(state => state.infoblock)

    const isFetched = infoblockState.isFetched
    const { data: infoblocks, error, isLoading } = useFetchAllInfoblocksQuery(10);

    if (isLoading) return <div>Loading...</div>;
    //   if (error) return <div>Error: {error}</div>;



    return (
        <React.Fragment>
            {
                infoblocks &&
                infoblocks.length &&
                infoblocks
                    .map((
                        infoblock: AIBlock,
                        i: number) => {

                        const isActive = i < 10
                        return <Infoblock
                            iblock={infoblock}
                            isActive={isActive}
                        />
                    })
            }
        </React.Fragment>

        // <div>
        //     {infoblock.map((group: DGroup) => (
        //         <div key={group.id}>
        //             <h2>{group.title}</h2>
        //             {/* {group.infoblocks.map((infoblock : AIBlock) => (
        //                 <p key={infoblock.id}>{infoblock.name}</p>
        //             ))} */}
        //         </div>
        //     ))}
        // </div>
    )
}


